/* ==========================================================================
   Repeat color variables for semantic mapping
   For example, you can use this file to globally change the colors
   of primary buttons and links;
   these are usually mapped to $color-primary at the component level.

   This is in a separate file because _s-color.scss is
   being used to generate documentation
   about the colors, and we technically 
   cannot read variables that reference other variables.
 ========================================================================== */

$color-primary: $dark-blue;
$color-link: $dark-blue;
$color-link: $dark-blue;
$g-primary-color-darker: #eee;
$g-primary-color-dark: #222;
$g-primary-color-light: #fff;
