/* ==========================================================================
   Elements: Base
   ---
   Only use elements without selectors when really necessary.
   ========================================================================== */

/* Elements
   ========================================================================== */

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 54.16%; // 13px
  @include sm { font-size: 56.25%; } // 13.5px
  @include md { font-size: 58.33%; } // 14px
  @include lg { font-size: 62.5%; } // Base 10

}

