body {
  font-family: $g-base-font-family;
  font-size: $g-base-font-size;
  line-height: $g-base-line-height;
  color: #333;
  background: #FFF;
  @media (prefers-color-scheme: dark) {
    color: #A0A4AE;
    background: #1B2130;
  }
  font-feature-settings: "liga", "kern";
}

img,
video {
  max-width: 100%;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
}

a {
  color: $color-link;
  text-decoration: none;
}

em, i {
  font-style: italic;
}

strong, b {
  font-weight: $g-headings-font-weight;
}

abbr[title] {
  text-decoration: none;
  opacity: 0.5;
}

img {
  max-width: 100%;
}

@media (prefers-color-scheme: dark) {
  img {
    filter: brightness(0.9) contrast(1.1);
  }
}

@mixin box-shadow($shadow...) {
    -webkit-box-shadow: $shadow;
    -moz-box-shadow: $shadow;       
    box-shadow: $shadow;
}

@mixin transition($attr, $duration, $timing) {
    -webkit-transition: $attr $duration $timing;
    -moz-transition: $attr $duration $timing;
    -o-transition: $attr $duration $timing;
    transition: $attr $duration $timing;
}
